.movie-listing {
  padding: 20px;
  background-color: #171717;
  color: #fff;
  font-family: "Titillium+Web";
}

@font-face {
  font-family: "Titillium";
  src: url('https://fonts.google.com/specimen/Titillium+Web');
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
  background-image: url("https://test.create.diagnal.com/images/nav_bar.png");
}

.title {
  font-size: 24px;
  font-weight: bold;
}

img {
  width: 30px;
}

.search {
  display: flex;
  align-items: center;
}

.search input {
  padding: 8px;
  font-size: 16px;
  border: 1px solid #fff;
  border-radius: 4px;
  margin-right: 10px;
}

.search-icon {
  cursor: pointer;
  font-size: 20px;
}

.movie-grid {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 20px;
}

.movie {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.poster {
  width: 100%;
  height: auto;
  border-radius: 8px;
}

.title {
  margin-top: 10px;
  text-align: center;
  font-size: 16px;
}
